.login__link {
    font-family: 'Inter', sans-serif, Arial;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #FFFFFF;
    text-decoration: none;
    transition: opacity 0.5s ease;
}

.login__link:hover {
    opacity: 0.6;
}